const accordion = (triggerSelector, itemsSelector) => {
	const buttons = document.querySelectorAll(triggerSelector),
		blocks = document.querySelectorAll(itemsSelector);

	//* JS + CSS styles for Accordion
	// blocks.forEach((block) => {
	// 	block.classList.add("animated", "fadeInDown");
	// });
	// buttons.forEach((button) => {
	// 	button.addEventListener("click", function () {
	// 		if (!this.classList.contains("active")) {
	// 			buttons.forEach((button) => {
	// 				button.classList.remove("active", "active-style");
	// 			});
	// 			this.classList.add("active", "active-style");
	// 		}
	// 	});
	// });

	//* Only JS styles for Accordion
	buttons.forEach((button) => {
		button.addEventListener("click", function (e) {
			buttons.forEach((button) => {
				button.classList.remove("active-style");
				button.nextElementSibling.classList.remove("active-content");
				button.nextElementSibling.style.maxHeight = "0px";
			});
			this.classList.add("active-style");
			this.nextElementSibling.classList.add("active-content");
			if (this.classList.contains("active-style")) {
				this.nextElementSibling.style.maxHeight =
					this.nextElementSibling.scrollHeight + 80 + "px";
			} else {
				this.nextElementSibling.style.maxHeight = "0px";
			}
		});
	});
};

export { accordion };
