import { postData } from "../services/requests";

const forms = () => {
	const forms = document.querySelectorAll("form"),
		inputs = document.querySelectorAll("input"),
		upload = document.querySelectorAll("[name='upload']"),
		calcSelects = document.querySelectorAll(".calc_form select");

	// checkNumInputs("input[name='user_phone']");

	const message = {
		loading: "Loading...",
		sucess: "Success",
		failure: "Oh no, Failure",
		spinner: "assets/img/spinner.gif",
		ok: "assets/img/ok.png",
		fail: "assets/img/fail.png",
	};

	const path = {
		designer: "assets/server.php",
		question: "assets/question.php",
	};

	const clearInputs = () => {
		inputs.forEach((input) => {
			input.value = "";
		});
		upload.forEach((item) => {
			item.previousElementSibling.textContent = "Файл не выбран";
		});
		calcSelects.forEach((select) => {
			select.value = "";
		});
	};

	upload.forEach((element) => {
		element.addEventListener("input", (e) => {
			let dots;
			element.files[0].name.split(".")[0].length > 6
				? (dots = "...")
				: (dots = ".");

			const name = `${element.files[0].name
				.split(".")[0]
				.slice(0, 10)}${dots}.${element.files[0].name.split(".")[1]}`;

			element.previousElementSibling.textContent = name;
		});
	});

	forms.forEach((form) => {
		form.addEventListener("submit", (e) => {
			e.preventDefault();

			let statusMessage = document.createElement("div");
			statusMessage.classList.add("status");
			form.parentNode.appendChild(statusMessage);

			form.classList.add("animated", "fadeOutUp");
			setTimeout(() => {
				form.style.display = "none";
			}, 400);

			let statusImg = document.createElement("img");
			statusImg.setAttribute("src", message.spinner);
			statusImg.classList.add("animated", "fadeInUp");
			statusMessage.appendChild(statusImg);

			let textMessage = document.createElement("div");
			textMessage.textContent = message.loading;
			statusMessage.appendChild(textMessage);

			const formData = new FormData(form);

			const calcPrice = document.querySelector(".calc-price").textContent;
			formData.append("price", calcPrice);

			let api;

			form.closest(".popup-design") || form.classList.contains("calc_form")
				? (api = path.designer)
				: (api = path.question);

			postData(api, formData)
				.then((res) => {
					console.log(res);
					statusImg.setAttribute("src", message.ok);
					textMessage.textContent = message.sucess;
				})
				.catch(() => {
					statusImg.setAttribute("src", message.fail);
					textMessage.textContent = message.failure;
				})
				.finally(() => {
					clearInputs();
					setTimeout(() => {
						statusMessage.remove();
						form.style.display = "block";
						form.classList.remove("fadeOutUp");
						form.classList.add("fadeInUp");
					}, 5000);
				});
		});
	});
};

export default forms;
