const sliders = (
	slides,
	dir,
	prev,
	next,
	slideIndex = 0,
	slidesStep = 1,
	stepInterval = 4000
) => {
	const slidesItems = document.querySelectorAll(slides);
	let paused = false,
		totalSlides = slidesItems.length - 1;

	function showSlides(index) {
		if (index > totalSlides) {
			slideIndex = 0;
		}
		if (index < 0) {
			slideIndex = totalSlides;
		}

		slidesItems.forEach((slide) => {
			slide.classList.add("animated");
			slide.style.display = "none";
		});

		slidesItems[slideIndex].style.display = "block";
	}

	showSlides(slideIndex);

	function plusSlides(n) {
		showSlides((slideIndex += n));
	}

	try {
		const prevBtn = document.querySelector(prev),
			nextBtn = document.querySelector(next);

		prevBtn.addEventListener("click", () => {
			plusSlides(-slidesStep);
			slidesItems[slideIndex].classList.remove("slideInLeft");
			slidesItems[slideIndex].classList.add("slideInRight");
		});
		nextBtn.addEventListener("click", () => {
			plusSlides(slidesStep);
			slidesItems[slideIndex].classList.remove("slideInRight");
			slidesItems[slideIndex].classList.add("slideInLeft");
		});
	} catch (e) {}

	function activateInterval() {
		if (dir === "vertical") {
			paused = setInterval(() => {
				plusSlides(slidesStep);
				slidesItems[slideIndex].classList.add("slideInDown");
			}, stepInterval);
		} else {
			paused = setInterval(() => {
				plusSlides(slidesStep);
				slidesItems[slideIndex].classList.remove("slideInRight");
				slidesItems[slideIndex].classList.add("slideInLeft");
			}, stepInterval);
		}
	}

	slidesItems[slideIndex].parentNode.addEventListener("mouseenter", () => {
		clearInterval(paused);
	});
	slidesItems[slideIndex].parentNode.addEventListener("mouseleave", () => {
		activateInterval();
	});

	activateInterval();
};

export default sliders;
