const modals = () => {
	let btnPressed = false;
	function bindModal(
		triggerSelector,
		modalSelector,
		closeSelector,
		destroy = false
	) {
		const trigger = document.querySelectorAll(triggerSelector),
			modal = document.querySelector(modalSelector),
			close = document.querySelector(closeSelector),
			windows = document.querySelectorAll("[data-modal]"),
			scroll = calcScroll();

		trigger.forEach((item) => {
			item.addEventListener("click", (e) => {
				if (e.target) {
					e.preventDefault();
				}

				btnPressed = true;

				if (destroy) {
					item.remove();
				}

				windows.forEach((window) => {
					window.style.display = "none";
					window.classList.add("animated", "fadeIn");
				});

				modal.style.display = "block";
				document.body.style.overflow = "hidden";
				document.body.style.marginRight = `${scroll}px`;
				// document.body.classList.add("modal-open");
			});
		});

		close.addEventListener("click", (e) => {
			windows.forEach((window) => {
				window.style.display = "none";
			});

			modal.style.display = "none";
			document.body.style.overflow = "";
			document.body.style.marginRight = `0px`;
			// document.body.classList.remove("modal-open");
			document.body;
		});

		modal.addEventListener("click", (e) => {
			if (e.target === modal) {
				windows.forEach((window) => {
					window.style.display = "none";
				});

				modal.style.display = "none";
				document.body.style.overflow = "";
				document.body.style.marginRight = `0px`;
			}
		});
	}

	function showModalByTimeout(selector, timeout) {
		setTimeout(() => {
			let display;
			document.querySelectorAll("[data-modal]").forEach((modal) => {
				if (getComputedStyle(modal).display !== "none") {
					display = "block";
				}

				if (!display) {
					document.querySelector(selector).style.display = "block";
					document.body.style.overflow = "hidden";
				}
			});

			document.querySelector(selector).style.display = "block";
			document.body.style.overflow = "hidden";
			const scroll = calcScroll();
			document.body.style.marginRight = `${scroll}px`;
		}, timeout);
	}

	function calcScroll() {
		let div = document.createElement("div");

		div.style.width = "50px";
		div.style.height = "50px";
		div.style.overflowY = "scroll";
		div.style.visibility = "hidden";

		document.body.appendChild(div);
		let scrollWidth = div.offsetWidth - div.clientWidth;
		div.remove();

		return scrollWidth;
	}

	function openByScroll(selector) {
		window.addEventListener("scroll", () => {
			let pageHeight = document.documentElement.scrollHeight;
			let bodyHeight = document.body.scrollHeight;
			let clientHeight = document.documentElement.clientHeight;
			let scrollY = window.scrollY;

			let maxHeight = Math.max(bodyHeight, pageHeight);

			if (!btnPressed && scrollY + clientHeight >= maxHeight) {
				// console.log("Page height", pageHeight);
				// console.log("Body height", bodyHeight);
				// console.log("Client scroll", clientHeight);
				// console.log("Total scroll", scrollY + clientHeight);
				// console.log("********************************");
				document.querySelector(selector).click();
			}
		});
	}

	bindModal(".button-design", ".popup-design", ".popup-design .popup-close");
	bindModal(".fixed-gift", ".popup-gift", ".popup-gift .popup-close", true);
	bindModal(
		".button-consultation",
		".popup-consultation",
		".popup-consultation .popup-close"
	);

	openByScroll(".fixed-gift");

	// showModalByTimeout(".popup-design", 5000);
};

export default modals;
