const checkTextInput = (selector) => {
	const txtInputs = document.querySelectorAll(selector);
	txtInputs.forEach((textInput) => {
		textInput.addEventListener("keypress", (e) => {
			if (e.key.match(/[^а-яё 0-9]/gi)) {
				e.preventDefault();
			}
		});
	});
};

export default checkTextInput;
