const calc = (size, material, options, promocode, result) => {
	const sizeBlock = document.querySelector(size),
		materialBlock = document.querySelector(material),
		optionsBlock = document.querySelector(options),
		promocodeBlock = document.querySelector(promocode),
		resultBlock = document.querySelector(result);

	let resultSum = 0;

	const calcFn = () => {
		let sizeValue = Number(sizeBlock.value),
			materialBlockValue = Number(materialBlock.value),
			optionsBlockValue = Number(optionsBlock.value);

		resultSum = Math.round(sizeValue * materialBlockValue + optionsBlockValue);
		if (sizeBlock.value == "" || materialBlock.value == "") {
			resultBlock.textContent = "Выберите разиеры и материал портрета.";
		} else if (promocodeBlock.value === "IWANTPOPART") {
			resultBlock.textContent = Math.round(resultSum * 0.7);
		} else {
			resultBlock.textContent = resultSum;
		}
	};

	sizeBlock.addEventListener("change", calcFn);
	materialBlock.addEventListener("change", calcFn);
	optionsBlock.addEventListener("change", calcFn);
	promocodeBlock.addEventListener("input", calcFn);

	return resultSum;
};

export { calc };
